body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body, html, #root{
  height: 100%;
  overflow: hidden;
}

*{
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul{
	list-style: none;
	margin: 0;
	padding: 0;
}

li{
	list-style: none;
}
.App {
	width: 90%;
	margin: 0 auto;
	padding: 3% 0;
	height: 100%;
}

.left{
	width: 30%;
	float: left;
	display: inline-block;
	height: 100%;
}

.right{
	width: 70%;
	display: inline-block;
	float: right;
	height: 100%;
}
.Point{
	padding: 5px;
	margin: 1px 0px;
	color: #333333;
	background: #ffffff;
	min-height: 30px;
}


.Point_focus{
	-webkit-transition: box-shadow 3s line 2s;
	transition: box-shadow 3s line 2s;
	box-shadow: 0px 10px 50px 5px rgba(0, 0, 0, 0.25);
	-webkit-animation: Point_shadow 400ms 1 ease;
	        animation: Point_shadow 400ms 1 ease;
}

@-webkit-keyframes Point_shadow{
	from{
		box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
	}
	to{
		box-shadow: 0px 10px 50px 5px rgba(0, 0, 0, 0.25);
	}
}

@keyframes Point_shadow{
	from{
		box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0);
	}
	to{
		box-shadow: 0px 10px 50px 5px rgba(0, 0, 0, 0.25);
	}
}

.Point__Text{
	padding: 0;
	margin: 0;
	width: calc(100% - 25px);
	overflow: hidden;
	display: inline-block;
}

.Point__Close{
	background: #ffffff;
	border: none;
	float: right;
	padding: 2px;
}

.Point__Close>span {
	position: relative;
	width: 16px;
	height: 16px;
	opacity: 0.5;
	float: right;
}

.Point__Close>span:hover {
	opacity: 1;
}

.Point__Close>span:before, .Point__Close>span:after {
	position: absolute;
	top: 2px;
	left: 7px;
	content: ' ';
	height: 12px;
	width: 2px;
	background-color: #333333;
}

.Point__Close>span:before {
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}

.Point__Close>span:after {
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.Add{
	width: 100%;
	background: #eee;
	padding: 5px;
	height: 40px;
}

.Add__Input{
	height: 100%;
	width: calc(100% - 60px);
	border: none;
	padding: 5px;
}

.Add__Button{
	font-size: 15px;
	height: 100%;
	width: 50px;
	background: #fff;
	border: none;
	float: right;
	color: #439fc2;
	font-weight: 700;
}
.List{
	width: 100%;
	height: calc(100% - 40px);
	background: #bfedff;
	padding: 5px;
	overflow: auto;
}

